import { Stack } from '@mui/material';
import { Link } from 'react-router-dom';

import { logo } from '../utils/constants';
import SearchBar from './SearchBar';


const Navbar = () => (
  <Stack direction='row'
    alignItems="center"
    p={2}
    sx={{ postion: 'sticky',  op: 0, justifyContent: 'space-between', 
    // background: 'linear-gradient(140deg, rgba(164,137,137,1) 3%, rgba(0,0,0,0.8421743697478992) 74%, rgba(117,49,49,1) 94%)',
    background: 'linear-gradient(90deg, rgba(33,28,28,1) 40%, rgba(0,0,0,1) 87%, rgba(25,6,6,0.8757878151260504) 94%)',
     boxShadow:'10px 10px 8px #353b48',
     }}>
    <Link to='/' style={{ display: 'flex', alignItems: 'center' }}>
      <img src= {logo} alt="logo" height={45}  />
    </Link>
    <SearchBar />


  </Stack>




)


export default Navbar