import { useState, useEffect } from 'react';
import { Box, Stack, Typography } from '@mui/material';
import { Sidebar, Videos } from "./"
import { FetchFromAPI } from '../utils/FetchFromAPI';

const Feed = () => {
  const [selectedCategory, setSelectedCategory] = useState("New");
  const [videos, setVideos] = useState([]);

  useEffect(() => {
    FetchFromAPI(`search?part=snippet&q=${selectedCategory}`)
      .then((data) => setVideos(data.items))
  }, [selectedCategory])



  return (
    <Stack sx={{ flexDirection: { sc: "column", md: "row" } }}>
      <Box sx={{ height: { sx: 'auto', md: '92vh' }, 
      // borderRight: '1px solid #3d3d3d',
       px: { sx: 0, md: 2 }, background: 'linear-gradient(90deg, rgba(33,28,28,1) 40%, rgba(0,0,0,1) 87%, rgba(25,6,6,0.8757878151260504) 94%)', borderBottomRightRadius: "30px" }}>
        <Sidebar
          selectedCategory={selectedCategory} setSelectedCategory={setSelectedCategory} />

        <Typography className='copyright'
          variant='body2'
          sx={{ mt: 1.5, color: "#fff", paddingRight: "6px", }}>
          Copyright 2023 NYX Media
        </Typography>
      </Box>

      <Box p={2} sx={{
        overflowY: 'auto',
        height: "90vh", flex: 2,
        // background: 'linear-gradient(140deg, rgba(164,137,137,1) 3%, rgba(0,0,0,0.8421743697478992) 36%, rgba(176,53,53,1) 94%)',
        // background: '#212121',
        background: 'linear-gradient(140deg, rgba(33,33,33,1) 73%, rgba(54,5,14,1) 94%)',
      }}>
        <Typography variant='h4'
          fontWeight="bold" mb={2}
          sx={{
            color: 'white', paddingLeft: "15px",
            borderRadius: "6px",
            fontFamily: " Verdana"
          }}>
          {selectedCategory} <span style={{ color: '#f31503' }}>
            Videos
          </span>
        </Typography>
        <Videos videos={videos} />
      </Box>





    </Stack >
  )
}

export default Feed